<!-- Cookies记录-->
<template>
  <div class="check_tab">
    <el-tabs class="tab_class" type="border-card">
      <el-tab-pane label="Cookies记录">
        <el-table
          :data="tableData"
          ref="multipleTable"
          tooltip-effect="dark"
          style="width: 100%"
          border
          stripe
          @selection-change="handleSelectionChange"
        >
          <el-table-column label="序号" width="50" type="index" sortable></el-table-column>
          <el-table-column prop="fileName" label="Cookie文件名" sortable width="150"></el-table-column>
          <el-table-column prop="keyWords" label="网址" sortable></el-table-column>
          <el-table-column prop="hitRate" label="上网时间" sortable min-width="110px"></el-table-column>
          <el-table-column prop="classified" label="浏览器名称" sortable min-width="120px"></el-table-column>
          <el-table-column prop="classified" label="浏览器版本" sortable min-width="120px"></el-table-column>
          <el-table-column prop="classified" label="浏览器内核" sortable min-width="120px"></el-table-column>
          <el-table-column prop="classified" label="访问账户" sortable min-width="110px"></el-table-column>
          <el-table-column prop="classified" label="Cookie文件路径" sortable min-width="130px"></el-table-column>
        </el-table>
      </el-tab-pane>
      <el-tab-pane label="Cookies统计">
        <el-table
          ref="multipleTable"
          :data="tableData"
          tooltip-effect="dark"
          style="width: 100%"
          @selection-change="handleSelectionChange"
          border
        >
          <el-table-column label="序号" width="50" type="index" sortable></el-table-column>
          <el-table-column prop="fileName" label="浏览器名称" sortable width="150"></el-table-column>
          <el-table-column prop="keyWords" label="上网数量" sortable></el-table-column>
        </el-table>
      </el-tab-pane>
    </el-tabs>
    <div class="check_dispose">
      <el-button @click="showDialog = true">设置检查时间</el-button>
      <el-select v-model="value1" placeholder="下载模板">
        <el-option
          v-for="item in options"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        ></el-option>
      </el-select>
      <el-select v-model="value2" placeholder="可信网址管理">
        <el-option
          v-for="item in options2"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        ></el-option>
      </el-select>
      <el-select v-model="value3" placeholder="导入可信网址">
        <el-option
          v-for="item in options3"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        ></el-option>
      </el-select>
    </div>
    <time-dialog :dialogVisible.sync="showDialog"></time-dialog>
  </div>
</template>

<script>
import timeDialog from "@/components/timeDialog";
export default {
  components: { timeDialog },
  data() {
    return {
      tableData: [
        // {
        //   fileName: "", //文件名称
        //   keyWords: "", //命中关键词
        //   hitRate: "", //关键词命中率
        //   classified: "", //涉密情况
        // }
      ],
      showDialog: false,
      options: [
        {
          value: "选项1",
          label: "下载可信网址列表模板"
        }
      ],
      value1: "",
      options2: [
        {
          value: "选项1",
          label: "添加可信网址"
        },
        {
          value: "选项2",
          label: "修改可信网址"
        }
      ],
      value2: "",
      options3: [
        {
          value: "选项1",
          label: "导入可信网址列表"
        }
      ],
      value3: ""
    };
  },
  methods: {
    handleSelectionChange(val) {
      this.multipleSelection = val;
    }
  }
};
</script>

<style lang="less" scoped>
.tab_class /deep/.el-tabs__nav {
  height: auto !important;
}
.tab_class /deep/.el-tabs__nav-scroll {
  height: auto;
}

/deep/.el-tabs__content {
  height: calc(~"(100% - 72px)"); /* less写法 */
}
/deep/.el-tab-pane {
  height: 100%;
}
/deep/.cell {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
/deep/.el-table td,
/deep/.el-table th {
  // text-align: left;
  padding: 0;
  height: 30px;
  line-height: 30px;
  color: #606266;
}
/deep/.el-table--fit {
  height: 100%;
}
/deep/.el-table__body-wrapper {
  height: 91%;
  overflow: auto;
}
.check_tab {
  height: 100%;
  position: relative;
  margin-left: 5px;
  .check_dispose {
    position: absolute;
    right: 20px;
    top: 4px;
    z-index: 100;

    .el-button {
      height: 30px;
      line-height: 0px;
      //   margin-top: 8px;
      font-size: 12px;
    }
    .el-select {
      width: 116px;
      margin-left: 5px;
      /deep/.el-input--suffix {
        margin-top: 8px;
      }
      /deep/.el-input__inner {
        height: 30px;

        font-size: 12px;
      }
      /deep/.el-input__icon {
        line-height: 30px;
      }
    }
  }
}
.tab_class {
  height: 100%;
  padding: 5px;
  box-sizing: border-box;
}
</style>
