 Dec 设置检查时间弹出框 -->
<template>
  <div class="check_dialog">
    <el-dialog
      :modal="false"
      :visible="dialogVisible"
      :modal-append-to-body="false"
      :append-to-body="true"
      :show-close="false"
      :close-on-click-modal="false"
      top="38vh"
      @close="onClose()"
    >
      <div slot="title" class="title_class">
        <span>
          <img src="/images/config_dlg_title.png" alt />
        </span>
        <span>常规USB检查配置</span>
      </div>
      <div class="content_class">
        <div class="check_type">
          <span>检查范围</span>
        </div>
        <div class="time_class common">
          <el-checkbox v-model="timeChecked" @change="unlimitedTimeChange">不限时间</el-checkbox>
          <div style="padding:10px 0 0">
            <span style="padding: 0 13px 0 18px;">开始时间</span>
            <el-date-picker
              v-model="startTime"
              type="datetime"
              placeholder="选择日期时间"
              size="mini"
              :disabled="timeDiable"
            ></el-date-picker>
            <span style="padding:0 18px 0 18px  ">结束时间</span>
            <el-date-picker
              v-model="endTime"
              type="datetime"
              placeholder="选择日期时间"
              size="mini"
              :disabled="timeDiable"
            ></el-date-picker>
          </div>
        </div>
      </div>
      <div slot="footer" class="footer_class">
        <el-button size="mini" @click="onClose()">应用</el-button>
        <el-button size="mini" @click="onClose()">取 消</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  props: ["dialogVisible"],
  components: {},
  data() {
    return {
      timeDiable: true, //不限时间输入框
      timeChecked: true,
      startTime: "",
      endTime: ""
    };
  },
  methods: {
    // 取消添加弹框
    onClose() {
      if (this.dialogVisible) {
        this.$emit("update:dialogVisible", false);
      }
    },
    // 不限时间选框按钮
    unlimitedTimeChange(val) {
      console.log(val);
      this.timeDiable = !this.timeDiable;
    }
  },
  computed: {}
};
</script>

<style lang="less" scoped>
