  background: #e4eef8;

  padding: 0 28px 6px;
}
/deep/.el-tabs__header {
  margin: 0;
}

.title_class {
  line-height: 70px;

  margin: auto;
  > span:nth-child(2) {
    line-height: 40px;
    font-size: 22px;
    font-weight: 800;
    line-height: 70px;
  }
  display: flex;
  justify-content: center;
}
.el-icon-picture-outline-round {
  padding-right: 3px;
  border-right: 1px solid;
  font-size: 40px;
}
.footer_class {
  display: flex;
  justify-content: space-around;
}
.content_class {
  margin: auto;
  background: #fff;
  padding: 5px;
}
